@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
:root {
  --primary-white: #fff;
  --primary-blue: #7fcff4;
  --primary-dark: rgb(255, 255, 255), 0;
  --primary-blue-gray: /*#E9E4F1*/ /*#ECF8FD*/ #ebf4ff;
  --primary-green: green;
  --secondary-blue: #01479d;
  --secondary-blue-hover: #002551;
  --primary-black: #000914;
  --transparent-black: rgb(0, 0, 0, 0.7);
}

body {
  font-family: 'Roboto', sans-serif !important;
}

/* NavBar*/
nav {
  z-index: 2;
}

.logo {
  width: 8.25rem;
}

/* Menu links*/

nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
  font-size: 1rem;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgb(255, 255, 255), 0;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.1875rem none #7fcff4;
  border-top: 0.1875rem none var(--primary-blue);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #7fcff4;
  border-top: 0.1875rem solid var(--primary-blue);
}

/* Header*/

.header-wraper {
  position: relative;
  background: url(/static/media/blk.406d0970.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: #7fcff4;
  color: var(--primary-blue);
  text-transform: uppercase;
  font-size: 3rem;
}

/* Typed Text */

.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}

.btn-main-offer {
  border: 1px solid #01479d;
  border: 1px solid var(--secondary-blue);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  background-color: #01479d;
  background-color: var(--secondary-blue);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #002551;
  background-color: var(--secondary-blue-hover);
  color: #fff;
  color: var(--primary-white);
  transition: 0.2s ease-in-out;
  border: 1px solid #002551;
  border: 1px solid var(--secondary-blue-hover);
}

/*Particles*/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}

/* About Me */
.photo-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.profile-img {
  width: 18rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid #7fcff4;
  border: 1px solid var(--primary-blue);
  margin-left: -6rem;
}

.about-heading {
  color: #7fcff4;
  color: var(--primary-blue);
  text-transform: uppercase;
  font-weight: 300;
}

.about-p {
  font-weight: 300;
}

@media (max-width: 468px) {
  .profile-img {
    width: 10rem;
    margin-left: 0rem;
  }
}

@media (max-width: 768px) {
  .profile-img {
    margin-left: 0rem;
  }
}

/* Services */
.services {
  text-align: center;
  padding: 2rem;
  background: #ebf4ff;
  background: var(--primary-blue-gray);
}

.services h1 {
  color: rgb(255, 255, 255), 0;
  color: var(--primary-dark);
  text-transform: uppercase;
  font-weight: 300;
}

.services h3 {
  font-weight: 300;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #7fcff4;
  background: var(--primary-blue);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.25rem;
}

.services .icon {
  font-size: 1.4rem;
  color: #fff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-bottom: 0.312rem solid #7fcff4;
  border-bottom: 0.312rem solid var(--primary-blue);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #fff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services p {
  font-weight: 300;
}

.services .box:hover {
  background: #7fcff4;
  background: var(--primary-blue);
  border-bottom: 0.3125rem solid #002551;
  border-bottom: 0.3125rem solid var(--secondary-blue-hover);
}

.services .box:hover .icon {
  color: rgb(255, 255, 255), 0;
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: #fff;
  background: var(--primary-white);
}

.services .box:hover h3 {
  font-weight: 400;
}

.services .box:hover p {
  font-weight: 400;
}

@media (max-width: 768px) {
  .services h3 {
    font-size: 1.3rem;
    font-weight: 400;
  }
}

/* experience */
.experience {
  padding: 0.7rem;
}
.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #7fcff4;
  color: var(--primary-blue);
  font-weight: 300;
}

.experience-wrapper::before {
  position: absolute;
  content: '';
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #002551;
  background: var(--secondary-blue-hover);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.325rem solid #002551;
  border: 0.325rem solid var(--secondary-blue-hover);
  background: #7fcff4;
  background: var(--primary-blue);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0 0.9375rem;
  color: #002551;
  color: var(--secondary-blue-hover);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #01479d;
  color: var(--secondary-blue);
  font-weight: 400;
}

/* Portfolio */

.portfolio-wrapper {
  background: #ebf4ff;
  background: var(--primary-blue-gray);
  padding: 3rem 0;
}

.portfolio-image {
  width: 15rem;
  height: 13rem;
  border: 2px solid #7fcff4;
  border: 2px solid var(--primary-blue);
  padding: 0 0;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

#view {
  text-align: center;
  padding-top: 3rem;
  
}

/* Overflow Box */

.overflow {
  position: absolute;
  opacity: 0;
  background: #000914;
  background: var(--primary-black);
  width: 15rem;
  height: 13rem;
  top: 0;
  border: 1px solid #01479d;
  border: 1px solid var(--secondary-blue);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 5.5rem;
  color: #7fcff4;
  color: var(--primary-blue);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
.popupbox-wrapper {
  margin-top: 12rem;
  margin-bottom: 5rem;
}


/*Popup Box */
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer;
  color: #7fcff4;
  color: var(--primary-blue);
  text-align: center;
}

.popup-text {
  padding-top: .5rem;
}

.hyper-text {
  text-align: center;
}

.hyper-link:hover {
  color: #002551;
  color: var(--secondary-blue-hover);
}

/* addtional media Queries */

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }

  .typed-text {
    font-size: 1.3rem;
  }
}

@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

@media (max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }

  .timeline-block-right {
    float: none;
    width: 100%;
  }

  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

@media (max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}
@media (max-height: 500px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 28%;
  }

  .portfolio-image-box {
    padding-left: 5rem;
  }
}

/* testimonials */

.testimonials {
  background-image: url(/static/media/bluepaint.01f0cf5f.jpg);
  background-size: cover;
  background-color: #000914;
  background-color: var(--primary-black);
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: #7fcff4;
  color: var(--primary-blue);
  padding: 3rem, 0;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: rgb(0, 0, 0, 0.7);
  background: var(--transparent-black);
}

.myCarousel {
  color: #fff;
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  height: 17.85rem;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
}

.carousel .slide img {
  width: 10.5rem !important;
  border-radius: 50%;
}

#ncswhite {
  height: 10rem !important;
  width: 11.5rem !important;
}



.myCarousel h3 {
  color: #7fcff4;
  color: var(--primary-blue);
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
}

.myCarousel p {
  color: #ebf4ff;
  color: var(--primary-blue-gray);
}

.carouse-root {
  margin: auto !important;
  margin-top: 3% !important;
}

.carousel .slide {
  background: transparent !important;
  height: 100%;
  padding-top: 2rem;
}

/*    Contacts */

.contacts {
  background: #ebf4ff;
  background: var(--primary-blue-gray);
  padding: 3rem 0;
}

.contacts h1 {
  color: #002551;
  color: var(--secondary-blue-hover);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #002551;
  color: var(--secondary-blue-hover);
  font-size: 1.2rem;
}

/* form */

.contacts input,
.contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #002551;
  border-bottom: 0.125rem solid var(--secondary-blue-hover);
  border-radius: 0;
  color: #002551;
  color: var(--secondary-blue-hover);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin: 1rem;
  padding-left: 0 !important ;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus,
.contacts textarea:focus {
  background: transparent;
  color: #002551;
  color: var(--secondary-blue-hover);
  border-bottom: 0.125rem solid #002551;
  border-bottom: 0.125rem solid var(--secondary-blue-hover);
}

.contacts textarea {
  height: 12.5rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
  color: #002551;
  color: var(--secondary-blue-hover);
  font-size: 1.2rem;
}

.line {
  position: relative;
  width: 0;
  top: -0.8rem;
  height: 0.13rem;
  background: #7fcff4;
  background: var(--primary-blue);
  display: inline-block;
}

input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.25rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #fff;
  color: var(--primary-white);
  background: red;
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}

/* Footer */

.footer{
  background: #002551;
  background: var(--secondary-blue-hover);
  color: #ebf4ff;
  color: var(--primary-blue-gray);
  padding: 3rem;

}

.footer a {
  text-decoration: none;
  color: #ebf4ff;
  color: var(--primary-blue-gray);
}

.footer a:hover {
  text-decoration: none;
  color: #7fcff4;
  color: var(--primary-blue);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}

/* MEdia Queries */
@media (max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

